"use client"
import React, { useState } from "react";
import { toast } from "react-toastify";
import { createProduct } from '../../utils/supabaseServices';

const FormProduct = () => {
  const [image, setImage] = useState(null);
  const [image1, setImage1] = useState(null);
  const [image2, setImage2] = useState(null);
  const [product, setProduct] = useState({
    name: "",
    description: "",
    price: 0,
    fragance: "",
    intensity: "",
  });

  const handleImageSelect = (e, setImageFn) => {
    const file = e.target.files[0];
    if (file) {
      setImageFn(file);
    }
  };

  const handleSubmit = async () => {
    try {
      // Llamamos a la función para crear el producto
      const newProduct = await createProduct({
        name: product.name,
        description: product.description,
        price: product.price,
        fragance: product.fragance,
        intensity: product.intensity,
        image,
        image1,
        image2,
      });

      toast.success("Producto creado exitosamente!");
    } catch (error) {
      toast.error("Error al crear el producto: " + error.message);
    }
  };

  return (
    <div>
      <form>
        <div>
          <label>Título</label>
          <input
            type="text"
            value={product.name}
            onChange={(e) => setProduct({ ...product, name: e.target.value })}
          />
        </div>
        <div>
          <label>Descripción</label>
          <textarea
            value={product.description}
            onChange={(e) => setProduct({ ...product, description: e.target.value })}
          />
        </div>
        <div>
          <label>Precio</label>
          <input
            type="number"
            value={product.price}
            onChange={(e) => setProduct({ ...product, price: e.target.value })}
          />
        </div>
        <div>
          <label>fragance</label>
          <input
            type="text"
            value={product.fragance}
            onChange={(e) => setProduct({ ...product, fragance: e.target.value })}
          />
        </div>
        <div>
          <label>intensity</label>
          <input
            type="text"
            value={product.intensity}
            onChange={(e) => setProduct({ ...product, intensity: e.target.value })}
          />
        </div>

        <div>
          <label>Imagen Principal</label>
          <input type="file" onChange={(e) => handleImageSelect(e, setImage)} />
        </div>

        <div>
          <label>Imagen Adicional 1</label>
          <input type="file" onChange={(e) => handleImageSelect(e, setImage1)} />
        </div>

        <div>
          <label>Imagen Adicional 2</label>
          <input type="file" onChange={(e) => handleImageSelect(e, setImage2)} />
        </div>

        <button type="button" onClick={handleSubmit}>
          Publicar Producto
        </button>
      </form>
    </div>
  );
};

export default FormProduct;
